import React, {useEffect, useState} from 'react';
import Button from "react-bootstrap/Button";
import Navigation from '../Components/Navigation';
import Container from "react-bootstrap/Container";
import {Col, DropdownButton, Row} from "react-bootstrap";
import Dropdown from "react-bootstrap/Dropdown";
import { CircleSlider } from "react-circle-slider";
import {Box, Slider} from "@mui/material";
import "./Dashboard.css";
import iro from "@jaames/iro";


const Dashboard = () => {

	// constants and variables

	const [currentMode, setCurrentMode] = useState({
		main: "manual-btn",
		secondary: "none",
	});
	let instUpdate = true;
	let currentColor = null;
	let selectedLedLeft = 1;
	let selectedLedRight = window.sessionStorage.getItem("ledNumber");

	// classes for elements

	class IroColorPicker extends React.Component {
		componentDidMount() {
			const { props } = this;
			// create a new iro color picker and pass component props to it
			this.colorPicker = new iro.ColorPicker(this.el, props);
			// call onColorChange prop whenever the color changes
			this.colorPicker.on("color:change", (color) => {
				// get color hsv with color.hsv
				console.log(color.hsv);
				// window.sessionStorage.setItem("color", color.hsv);
				if (props.onColorChange){
					props.onColorChange(color);
				}
				currentColor = color;
				updateLedPreview();
			});
		}

		componentDidUpdate() {
			// isolate color from the rest of the props
			const { color, ...colorPickerState } = this.props;
			// update color
			if (color) this.colorPicker.color.set(color);
			// push rest of the component props to the colorPicker's state
			this.colorPicker.setState(colorPickerState);
		}

		render() {
			console.log(this.props.onColorChange);
			return <div id="ColorPickerMainDiv" ref={(el) => (this.el = el)} />;
		}
	}

	// functions for elements

	function createLedArray(ledNumber){
		let array = [];
		for (let i = 0; i < ledNumber; i++){
			array.push(i);
		}
		return array;
	}


	const LedStrip = ({array}) => (
		<>
			{array.map( led => (
					<div id={led} key={led} className="color-indicator"/>
				)
			)}
		</>
	);

	function NormalSlider(){
		return(
			<Box height={200}>
				<Slider id="bpm-slider" defaultValue={60} min={0} max={255} orientation="vertical" valueLabelDisplay="auto" onChange={handleBpmChange}/>
			</Box>
		);
	}

	function DistanceSlider(){
		const minDistance = 1;
		const [value, setValue] = React.useState([minDistance, window.sessionStorage.getItem("ledNumber")]);

		const handleChange = (event, newValue, activeThumb) => {
			if (!Array.isArray(newValue)) {
				return;
			}
			if (newValue[1] - newValue[0] < minDistance) {
				if (activeThumb === 0) {
					const clamped = Math.min(newValue[0], 100 - minDistance);
					setValue([clamped, clamped + minDistance]);
				} else {
					const clamped = Math.max(newValue[1], minDistance);
					setValue([clamped - minDistance, clamped]);
				}
			} else {
				setValue(newValue);
			}
			selectedLedLeft = value[0];
			selectedLedRight = value[1];
		};

		return (
			<Box sx={{ width: 300 }}>
				<label id="led-selector-label"><h3 style={{color: `white`}}>LED range selector</h3></label>
				<Slider
					id="range-selector-slider"
					getAriaLabel={() => 'Minimum distance shift'}
					value={value}
					min={1}
					max={parseInt(window.sessionStorage.getItem("ledNumber"))}
					onChange={handleChange}
					valueLabelDisplay="auto"
					//disableSwap
				/>
			</Box>
		);
	}

	// action functions and listeners

	function btnPressed(el) {
		//console.log(el.target.id);
		let press;
		if(typeof el !== "object") {
			if (el.indexOf("audio-btn") > -1) {
				press = {
					main: "audio-btn",
					secondary: el.split(" ")[1],
				};
				console.log(press);
			}
		} else {
			press = {
				main: el.target.id,
				secondary: null,
			};
		}
		if (press.main !== "update-btn"){
			btnColorChange(press.main);
			setCurrentMode(press);
		} else {
			instUpdate = !instUpdate;
			let element = document.getElementById(press.main);
			if(!instUpdate){
				element.style.backgroundColor = "red";
			} else {
				element.style.backgroundColor = "green";
			}
		}
		//console.log(press.id, press.mode);
		//modeDependentStyle();
	}

	function btnAckEvent(){

	}

	function btnColorChange(element) {
		const btn_ids = ["manual-btn", "audio-btn", "strobe-btn", "bpm-btn", "rainbow-btn"];
		document.getElementById(element).style.backgroundColor = "green";
		for(let i = 0; i < 5; i++){
			if(btn_ids[i] !== element){
				document.getElementById(btn_ids[i]).style.backgroundColor = "red";
			}
		}
	}

	function bpmColorChange(el) {
		el.preventDefault();
		document.getElementById(el.target.id).style.backgroundColor = currentColor.hexString;
		updateLedPreview(parseInt(el.target.id.charAt(el.target.id.length - 1)) - 1);
	}

	function handleBpmChange(){

	}

	function frequencyChange(value) {

	}

	function updateLedPreview(i){
		if (currentMode.main !== "bpm"){
			if(currentMode.main === "manual-btn" && instUpdate){
				// eel.change_color([led_low_value, led_high_value], current_color.hsv);
				for(let k = (selectedLedLeft - 1); k < selectedLedRight	; k++){
					document.getElementById(k.toString()).style.backgroundColor = currentColor.hexString;
				}
			}
		} else {
			// eel.bpm_color_change(i, current_color.hsv);
			let quarter = Math.floor(window.sessionStorage.getItem("ledNumber") / 4);
			for(let j = (i * quarter); j < quarter + quarter * i; j++){
				document.getElementById(j.toString()).style.backgroundColor = currentColor.hexString;
			}
		}
	}

	useEffect(() => {
		if(currentMode.main === "strobe-btn" || currentMode.main === "rainbow-btn"){
			document.getElementById("frequency-container").style.display = "block";
		} else {
			document.getElementById("frequency-container").style.display = "none";
		}
		if(currentMode.main === "bpm-btn"){
			document.getElementById("bpm-container").style.display = "block";
			document.getElementById("bpm-picker-container").style.display = "flex";
		} else {
			document.getElementById("bpm-container").style.display = "none";
			document.getElementById("bpm-picker-container").style.display = "none";
		}
		if(currentMode.main !== "manual-btn"){
			document.getElementById("slider-container").style.display = "none";
		} else {
			document.getElementById("slider-container").style.display = "flex";
		}
	}, [currentMode])

	useEffect(() => {
		window.sessionStorage.setItem("ledNumber", 60);
	}, [])

	return (
		<div className="DashboardDiv">
			<Navigation/>
			<Container>
				<Row id="row0">
					<div id="led-array" className="container">
						<LedStrip array={createLedArray(window.sessionStorage.getItem("ledNumber"))}/>
					</div>
				</Row>
				<Row id="row1">
					<Col xs={12} lg={3}>
						<IroColorPicker/>
					</Col>
					<Col xs={5} lg={1}>
						<div id="main-effects-container1" className="container">
							<Button id="manual-btn" className="btn-primary" onClick={btnPressed}>Manual</Button>
							<DropdownButton title="Audio" id="audio-btn" onSelect={btnPressed}>
								<Dropdown.Item eventKey="audio-btn frequency">Frequency mode</Dropdown.Item>
								<Dropdown.Item eventKey="audio-btn single">Single color equalizer</Dropdown.Item>
								<Dropdown.Item eventKey="audio-btn gradient">Gradient equalizer</Dropdown.Item>
							</DropdownButton>
							<Button id="strobe-btn" className="btn-primary" onClick={btnPressed}>Stroboscope</Button>
						</div>
					</Col>
					<Col xs={5} lg={1}>
						<div id="main-effects-container2" className="container">
							<Button id="update-btn" className="btn-primary" onClick={btnPressed}>Instant update</Button>
							<Button id="bpm-btn" className="btn-primary" onClick={btnPressed}>BPM</Button>
							<Button id="rainbow-btn" className="btn-primary" onClick={btnPressed}>Rainbow</Button>
						</div>
					</Col>
					<Col id="macros-col1" lg={6}>
						<button id="btn0" className="btn btn-primary" onClick={btnAckEvent}>Effect</button>
						<button id="btn1" className="btn btn-primary" onClick={btnAckEvent}>Effect</button>
						<button id="btn2" className="btn btn-primary" onClick={btnAckEvent}>Effect</button>
						<button id="btn3" className="btn btn-primary" onClick={btnAckEvent}>Effect</button>
						<button id="btn4" className="btn btn-primary" onClick={btnAckEvent}>Effect</button>
						<button id="btn5" className="btn btn-primary" onClick={btnAckEvent}>Effect</button>
						<button id="btn6" className="btn btn-primary" onClick={btnAckEvent}>Effect</button>
						<button id="btn7" className="btn btn-primary" onClick={btnAckEvent}>Effect</button>
						<button id="btn8" className="btn btn-primary" onClick={btnAckEvent}>Effect</button>
						<button id="btn9" className="btn btn-primary" onClick={btnAckEvent}>Effect</button>
						<button id="btn10" className="btn btn-primary" onClick={btnAckEvent}>Effect</button>
						<button id="btn11" className="btn btn-primary" onClick={btnAckEvent}>Effect</button>
						<button id="btn12" className="btn btn-primary" onClick={btnAckEvent}>Effect</button>
						<button id="btn13" className="btn btn-primary" onClick={btnAckEvent}>Effect</button>
						<button id="btn14" className="btn btn-primary" onClick={btnAckEvent}>Effect</button>
					</Col>
				</Row>
				<Row id="row2">
					<Col id="range-slider-col" xs={12} lg={3}>
						<div id="bpm-picker-container" className="container">
							<div className="CurrentBpmColor" id="bpmIndicator1" onClick={bpmColorChange}/>
							<div className="CurrentBpmColor" id="bpmIndicator2" onClick={bpmColorChange}/>
							<div className="CurrentBpmColor" id="bpmIndicator3" onClick={bpmColorChange}/>
							<div className="CurrentBpmColor" id="bpmIndicator4" onClick={bpmColorChange}/>
						</div>
						<div id="slider-container" className="container">
							<DistanceSlider/>
						</div>
					</Col>
					<Col xs={12} lg={2}>
						<div id="frequency-container" className="container">
							<label id="frequency-label"><h2 style={{color: `white`}}>Frequency</h2></label>
							<CircleSlider id="frequency-slider" size={250} min={0} max={255} value={21} showTooltip={true} tooltipColor={"white"} onChange={value => frequencyChange(value)}/>
						</div>
						<div id="bpm-container" className="container">
							<label id="bpm-slider-label"><h3 style={{color: `white`}}>BPM</h3></label>
							<NormalSlider/>
						</div>
					</Col>
					<Col id="macros-col2" xs={0} lg={6}>
						<button id="btn15" className="btn btn-primary" onClick={btnAckEvent}>Effect</button>
						<button id="btn16" className="btn btn-primary" onClick={btnAckEvent}>Effect</button>
						<button id="btn17" className="btn btn-primary" onClick={btnAckEvent}>Effect</button>
						<button id="btn18" className="btn btn-primary" onClick={btnAckEvent}>Effect</button>
						<button id="btn19" className="btn btn-primary" onClick={btnAckEvent}>Effect</button>
						<button id="btn20" className="btn btn-primary" onClick={btnAckEvent}>Effect</button>
						<button id="btn21" className="btn btn-primary" onClick={btnAckEvent}>Effect</button>
						<button id="btn22" className="btn btn-primary" onClick={btnAckEvent}>Effect</button>
						<button id="btn23" className="btn btn-primary" onClick={btnAckEvent}>Effect</button>
						<button id="btn24" className="btn btn-primary" onClick={btnAckEvent}>Effect</button>
					</Col>
				</Row>
			</Container>
		</div>
	);
}

export default Dashboard;