import React, { useState } from 'react';
import Navigation from '../Components/Navigation';
import Picture from '../resources/picture.png';
import {Col, Form, Row} from "react-bootstrap";
import Button from "react-bootstrap/Button";
import { useNavigate } from 'react-router-dom';
import "./Home.css";


const Home = () => {
	const navigate = useNavigate();

	function handleClick() {
		navigate('/settings');
	}

	function handleClick2() {
		navigate('/diy');
	}

	return (
		<div className="HomeContainer">
			<div className="HomeFormContainer" style={{width: `100%`, height: `100vh` ,backgroundImage: `url(${Picture})`}}>
				<Row>
					<Col/>
					<Col>
						<Row>
							<h1 style={{color: `white`, marginTop: `2em`, marginBottom: `2em`}}>Welcome to LED Controller!</h1>
							<h2 style={{color: `white`, marginTop: `1em`, marginBottom: `2em`}}>The first online open source software to control your LED lights!</h2>
						</Row>
						<Row>
							<Button style={{backgroundColor: `green`, borderColor: `green`, width: `20em`}} onClick={handleClick}><h2>Run app</h2></Button>
							<Button style={{backgroundColor: `green`, borderColor: `green`, width: `20em`}} onClick={handleClick2}><h2>Build your own led controller</h2></Button>
						</Row>
					</Col>
					<Col/>
				</Row>
			</div>
		</div>
	);
}

export default Home;